import { AnyAction, isRejectedWithValue, Middleware, isRejected, MiddlewareAPI } from '@reduxjs/toolkit'
import { toast } from 'react-toastify'
import { isEntityError } from 'utils/helpers'

function isPayloadErrorMessage(payload: unknown): payload is {
  data: {
    error: string
  }
  status: number
} {
  return (
    typeof payload === 'object' &&
    payload !== null &&
    'data' in payload &&
    typeof (payload as any).data?.error === 'string'
  )
}

export const rtkQueryErrorLogger: Middleware = (api: MiddlewareAPI) => (next) => (action: AnyAction) => {
  /**
   * `isRejectedWithValue` l mt function gip chng ta kim tra nhng action c rejectedWithValue = true t createAsyncThunk
   * RTK Query s dng `createAsyncThunk` bn trong nn chng ta c th dng `isRejectedWithValue`  kim tra li 
   */

  // Option: Trong thc t khng bt buc n mc ny!
  if (isRejected(action)) {
    if (action.error.name === 'CustomError') {
      // Nhng li lin quan n qu trnh thc thi
      toast.warn(action.error.message)
    }
  }

  if (isRejectedWithValue(action)) {
    // Mi khi thc hin query hoc mutation m b li th n s chy vo y
    // Nhng li t server th action n mi c rejectedWithValue = true
    // Cn nhng action lin quan n vic caching m b rejected th rejectedWithValue = false, nn ng lo lng, n khng lt vo y c
    if (isPayloadErrorMessage(action.payload)) {
      // Li reject t server ch c message thi!
      toast.warn(action.payload.data.error)
    }
  }

  return next(action)
}
