import { FetchBaseQueryError } from '@reduxjs/toolkit/query'
import { error } from 'console'

/**
 * Kiu ErrorFormObject dnh cho trng hp bao qut
 */

interface ErrorFormObject {
  [key: string | number]: string | ErrorFormObject | ErrorFormObject[]
}

interface EntityError {
  status: 422
  data: {
    error: ErrorFormObject
  }
}

/**
 * Phng php "type predicate" dng  thu hp kiu ca mt bin
 *  u tin chng ta s khai bo mt function check kim tra cu trc v mc logic javascript
 *  Tip theo chng ta thm `parameterName is Type` lm kiu return ca function thay v boolean
 *  Khi dng function kiu tra kiu ny, ngoi vic kim tra v mc logic cu trc, n cn chuyn kiu
 *
 * So snh vi phng php p kiu "Type Assertions" th p kiu chng gip chng ta ng v mc Type, cha chc v logic
 *
 */

/**
 * Thu hp mt error c kiu khng xc nh v `FetchBaseQueryError`
 */

export function isFetchBaseQueryError(error: unknown): error is FetchBaseQueryError {
  return typeof error === 'object' && error !== null && 'status' in error
}

/**
 * Thu hp mt error c kiu khng xc nh v mt object vi thuc tnh message: string (SerializedError)
 */

export function isErrorWithMessage(error: unknown): error is { message: string } {
  return typeof error === 'object' && error !== null && 'message' in error && typeof (error as any).message === 'string'
}

/**
 * Thu hp mt error c kiu khng xc nh v li lin quan n POST PUT khng ng field (EntityError)
 */

export function isEntityError(error: unknown): error is EntityError {
  return (
    isFetchBaseQueryError(error) &&
    error.status === 422 &&
    typeof error.data === 'object' &&
    error.data !== null &&
    !(error.data instanceof Array)
  )
}

export class CustomError extends Error {
  constructor(message: string) {
    super(message)
    this.name = 'CustomError'
  }
}
